.header {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  background-color: #030301;
  margin: 20px auto;       /* fixed vertical spacing */
  border-radius: 60px;     /* fixed, no scaling */
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
  padding: 10px 20px;      /* fixed padding */
  width: 90%;              /* can still grow horizontally */
  height: 120px;           /* FIXED header height */
  box-sizing: border-box;
}
.logo-container {
  position: relative; 
  display: inline-block;
  flex-shrink: 0;
}

.logo {
  height: 100px;    /* fixed height */
  width: auto;      /* maintain aspect ratio */
  padding: 10px;
}

.logo-text {
  position: absolute;
  top: 75%;         /* relative to logo */
  left: 28%;        /* adjust relative to logo width */
  font-size: 18px;  /* fixed font size */
  font-weight: bold;
  color: white;
  margin-top: 5px;
}